<template>
  <div>
    <headerNav title="测试结果" />


    <div class="title">检查结果</div>
    <van-cell-group>
      <template v-for="(item, index) in info">
        <van-cell :key="index" :title="item.Label" :value="item.Val" />
      </template>
    </van-cell-group>


    <div class="title">就医建</div>
    <div class="title-val van-hairline">{{text}}</div>


    <div class="title">科普</div>
    <div class="title-val van-hairline">{{des}}</div>

  </div>
</template>

<script>
import { GetCoupon, ExchangeCoupon } from "../api/user.js";

export default {
  components: {},
  data() {
    return {
      loading: false,
      finished: false,
      list: [],
      page: 0,
      info: [],
      text: "",
      des: "",
      loading: false,
    };
  },
  computed: {},
  created(){
    this.onLoad()
  },
  methods: {
    onLoad() {
      this.page++;
      GetCoupon({ page: this.page }).then((response) => {    
        this.info = response.info;
        this.text = response.text;
        this.des = response.des;

        this.list = [];
        response.List.forEach((item) => {
          item.show = false;

          this.list.push(item);
        });
        this.loading = false;
        if (response.TotalPage <= this.page) {
          this.finished = true;
        }
      });
    },
    onShowInfo(id, index) {
      this.list.forEach((item, itemIndex) => {
        if (index == itemIndex) {
          item.show = !item.show;
          return;
        }
      });
    },
    onLoadUse() {
      this.usePage++;
      GetCoupon({ page: this.usePage }).then((response) => {
        response.List.forEach((item) => {
          this.useList.push(item);
        });
        this.useLoading = false;
        if (response.TotalPage <= this.usePage) {
          this.useFinished = true;
        }
      });
    },
    onLoadEnd() {
      this.endPage++;
      GetCoupon({ page: this.endPage }).then((response) => {
        response.List.forEach((item) => {
          this.endList.push(item);
        });
        this.endLoading = false;
        if (response.TotalPage <= this.endPage) {
          this.endFinished = true;
        }
      });
    },
    onExchange() {
      if (this.exchangeLoading) {
        return;
      }
      this.exchangeLoading = true;
      ExchangeCoupon(this.couponCode).then((response) => {
        this.$toast("兑换成功");
        this.exchangeLoading = false;
        this.$router.go(0);
      });
    },
    onClick(index, subindex, lineindex) {
      // console.log(  index,subindex,lineindex )
      let flag =
        this.list[index]["Coupon"][subindex]["line"][lineindex]["flag"];
      this.list[index]["Coupon"][subindex]["line"][lineindex]["flag"] = !flag;
    },
  },
};
</script>

